.logo {
  position: absolute;
  width: 150px;
  margin-top: 5px;
}

.navigation {
  list-style: none;
  margin: 0; 
  
  background: #3D3D3D;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.navigation a {
  text-decoration: none;
  display: block;
  padding: 1em;
  color: #aaa;
}

.navigation a:hover {
  color: white;
}

@media all and (max-width: 800px) {
  .navigation {
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
}

@media all and (max-width: 600px) {
  .logo {
    position: absolute;
    width: 150px;
    left: 50%;
    margin-left: -75px;
  }
  .navigation {
    -ms-flex-flow: column wrap;
        flex-flow: column wrap;
    padding: 50px 0 0 0 ;
  }
  
  .navigation a { 
    text-align: center; 
    padding: 10px;
    border-top: 1px solid rgba(255,255,255,0.3); 
    border-bottom: 1px solid rgba(0,0,0,0.1); 
  }

  
  .navigation li:last-of-type a {
    border-bottom: none;
  }
}


